import { Container, Typography } from "@material-ui/core";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router";

const ShowProduct = ({ product }) => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(false);
  const { category } = useParams();

  const filterFunction = (array, filter) => {
    return array.filter((arr) =>
      arr.link.includes(
        `https://20h20.fr/${filter}/${arr.title.rendered
          .replace(/(-[0-9])/g, "")
          .replace(/([èé])/g, "e")
          .replace(/([àÀ])/g, "a")
          .replace(/\s/g, "-")
          .replace(/'/g, "")
          .toLowerCase()}/`
      )
    );
  };

  useEffect(() => {
    setLoading(true);
    axios({
      method: "get",
      url: `https://20h20.fr/wp-json/wp/v2/${category}?page=1`,
      params: {
        status: ["publish"],
        per_page: "100",
        order: "asc",
      },
    })
      .then((response) => {
        if (response.headers["x-wp-totalpages"] < 2) {
          setProducts(filterFunction(response.data, category));
        } else {
          axios({
            method: "get",
            url: `https://20h20.fr/wp-json/wp/v2/${category}?page=2`,
            params: {
              status: ["publish"],
              per_page: "100",
              order: "asc",
            },
          })
            .then((response2) =>
              setProducts(
                filterFunction(response.data, category).concat(
                  filterFunction(response2.data, category)
                )
              )
            )
            .catch((error) => console.log(error));
        }
      })
      .catch((error) => console.log(error))
      .finally(() => setLoading(false));
  }, [category]);
  return (
    <Container>
      <Typography variant="h2">{product}</Typography>
      {products.sort((a, b) => b.meta.prix-a.meta.prix).map((product) => (
        <>
          <Typography variant="h5" style={{display: "flex", justifyContent: "space-between"}}>
            {product.title.rendered}
            <Typography variant="body1" component="span">
              {product.meta.prix}€
            </Typography>
          </Typography>
          <Typography variant="body2" component="p">
            {product.content.rendered.replace(/<[^>]*>?/gm, "")}
          </Typography>
        </>
      ))}
    </Container>
  );
};

export default ShowProduct;
