import { makeStyles } from "@material-ui/core";
import React from "react";
import { Link } from "react-router-dom";
import { formInfos } from "../../../addProductDatas/addProductDatas";

const NavigationBar = ({ setProduct }) => {
  const useStyles = makeStyles((theme) => ({
    nav: {
      overflowX: "auto",
      padding: "8px 0px",
      display: "flex",
    },
    ul: {
      padding: "0px 8px",
      margin: "auto",
      display: "flex",
      alignItems: "flex-start",
      listStyle: "none",
    },
    li: {
      width: "80px",
      alignItems: "center",
      display: "flex",
      flexDirection: "column",
      position: "relative",
    },
    imgWrapper: {
      height: "60px",
      width: "60px",
      position: "relative",
    },
    image: {
      height: "100%",
      width: "100%",
      borderRadius: "50%",
    },
    span: {
      lineHeight: "16px",
      fontSize: "14px",
      textAlign: "center",
      width: "100%",
    },
  }));
  const classes = useStyles();
  return (
    <nav className={classes.nav}>
      <ul className={classes.ul}>
        {formInfos.map((info) => (
          <Link
            key={info.categoryName}
            to={`/produits/${info.categoryName}`}
            onClick={() => setProduct(info.title)}
          >
            <li className={classes.li}>
              <div className={classes.imgWrapper}>
                <img
                  className={classes.image}
                  src={info.icon}
                  alt={info.categoryName}
                />
              </div>
              <span className={classes.span}>{info.title}</span>
            </li>
          </Link>
        ))}
      </ul>
    </nav>
  );
};

export default NavigationBar;
